import React,{ useContext, useState, useRef, useEffect } from 'react';
import { Threndcontext } from "../Context/Context";


const Addnewthrend = () => {
  
  const { addthrend } = useContext(Threndcontext);

  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");
  const [threndId, setThrendId] = useState(Date.now());
  const [inputError, setInputError] = useState(true);

let deactivate = !title || !content ?true:false;
let error = !title || !content ? "Please input your title and content":"";
 

  const getTitleRef = useRef();
  const getContentRef = useRef();

  const newThrend = {
      title,
      content,
      threndId,
      comments:[]
    }

  useEffect(() => { 
    setThrendId(Date.now());
  }, [content])



  return (
      <form onSubmit={(e)=>addthrend(e, newThrend, getTitleRef, getContentRef)}>
        <p><small>{error}</small></p>
        <input 
        className="title_input"
        type="text" 
        placeholder="Title"
        onChange={(e)=> setTitle(e.target.value)}
        ref={getTitleRef}
        />
        <textarea 
         className="content_input"
         placeholder="Content"
         onChange={(e)=> setContent(e.target.value) }
         ref={getContentRef}>
        </textarea>
        <input 
        type="submit" 
        value="Add thrend"
        disabled={deactivate}
        />
        
      </form>
  )
}

export default Addnewthrend;
