import React,{ useContext, useState } from 'react';
import { Threndcontext } from "../Context/Context";


const Comments = () => {

  const { thrends, addCommentToComments} = useContext(Threndcontext);
  const [ comment, setComment ] = useState("");


  const handleComments = (e)=>{
    setComment(e.target.value);
  }

  return (
    <div>
      {thrends.map((thrend, index)=>(
      <>
        <div className="thrend_selected">
          <p className="thrend_title">{thrend.title}</p>
          <p className="thrend_content">{thrend.content}</p>
        </div>
        <div key={index} className="comments">
          {thrend.comments.map(comments =>(
            <p>{comments}</p>
          ))}
        </div>
      </>
      ))}
      <form onSubmit={(e)=> addCommentToComments(e, comment)}>
          <input 
          onChange={handleComments}
          type="text" 
          value={comment}
          placeholder="Add New Comment"
          />
          <button>add comment</button>
      </form>
    </div>
  )
}

export default Comments
