import React, { createContext, useReducer, useEffect } from "react";
import Appreducer from "./Appreducer";


 const initialstate = {
    
  thrends:localStorage.getItem("thrends")?JSON.parse(localStorage.getItem("thrends")):
  [
    {
      title: "current afairs",
      content:"nigeria government is the worse government in the world",
      threndId:22,
      comments:[]
    },
  ]
}

export const Threndcontext =  createContext(initialstate);

export const ThrendProvider = (props)=>{

   const [state, dispatch] = useReducer( Appreducer, initialstate );

   useEffect(() => {
    localStorage.setItem("thrends", JSON.stringify(state.thrends));
  },[state])



   const addthrend = async (e, newThrend, getTitleRef, getContentRef)=>{
     e.preventDefault();
     dispatch({type: "ADD_NEW_THREND", payload: newThrend })
     getTitleRef.current.value= "";
     getContentRef.current.value= "";
     window.location.href = ("/my-blogpost");  

   }
   const removeThrend = (id)=>{
     dispatch({type:"REMOVE_THREND", payload:id})
   }
   const addCommentToComments = (e, comment)=>{
     e.preventDefault();
     dispatch({type: "ADD_COMMENT_TO_COMMENTS", action: comment});
   }

  return(
    <Threndcontext.Provider value={{
      thrends:state.thrends,
      addthrend,
      removeThrend,
      addCommentToComments
    }}>
      { props.children }
    </Threndcontext.Provider>
  )

}