export default (state, action) =>{
  switch (action.type) {
    case "ADD_NEW_THREND":
      return{
        ...state,
        thrends: [...state.thrends, action.payload ]
      }
    case "REMOVE_THREND":
      return{
        ...state,
        thrends: state.thrends.filter( thrend => thrend.threndId !== action.payload)
      }
      case "ADD_COMMENT_TO_COMMENTS":
        return{
        
        }
      break;
  
    default:
      return state;
  }
}