import React,{ useContext } from 'react';
import { Threndcontext } from "../Context/Context";
import { Link } from "react-router-dom";


function Thrend() {

const { thrends, removeThrend } = useContext(Threndcontext);

  return (
    <div className="thrends">
      {thrends.map((thrend, index)=>(
        <div key={index} className="thrend_container">
          <span 
          className="fas fa-times-circle"
          onClick={()=> removeThrend(thrend.threndId)}
          ></span>
        <p className="thrend_title">{thrend.title}</p>
        <p className="thrend_content">{thrend.content}</p>
        <div 
        className="comments_number">
          <span><Link to="/my-blogpost">comments {thrend.comments.length}</Link></span>
        </div>
        </div>
      ))}

      <div className="addthrends">
        <Link to="/add">Add Thrend</Link>
      </div>
      
    </div>
  )
}

export default Thrend;
