import React from 'react';
import { BrowserRouter as Router, Switch, Route} from "react-router-dom";
import { ThrendProvider } from "./Context/Context";
import Thrend from "./Components/Thrend";
import "./mystyle.css";
import Addnewthrend from './Components/Addnewthrend';
import Comments from './Components/Comments';


function App() {
 
  return (
    <ThrendProvider>
      <Router> 
        <Switch>
          <div className="App">
          <h1>Blog Thrend</h1>
            <Route exact path="/my-blogpost" component={Thrend} />
            <Route path="/add" component={Addnewthrend} />
            <Route path="/comments" component={Comments} />
          </div>
        </Switch>
      </Router>
    </ThrendProvider>
  );
 
}

export default App;
